"use client"

import { createContext, useContext, useEffect, useState, type ReactNode } from "react"
import type { Alternatif, Kriteria, SubKriteria, Penilaian, MetodeConfig, PairwiseComparison } from "./spk-types"
import { useToast } from "@/components/ui/ios-toast"

interface SPKContextType {
  // Data
  alternatif: Alternatif[]
  kriteria: Kriteria[]
  subKriteria: SubKriteria[]
  penilaian: Penilaian[]
  metodeConfig: MetodeConfig[]
  pairwiseComparisons: PairwiseComparison[]

  // Actions
  setAlternatif: (data: Alternatif[]) => void
  addAlternatif: (data: Omit<Alternatif, "id" | "createdAt">) => void
  updateAlternatif: (id: string, data: Partial<Alternatif>) => void
  deleteAlternatif: (id: string) => void

  setKriteria: (data: Kriteria[]) => void
  addKriteria: (data: Omit<Kriteria, "id" | "createdAt">) => void
  updateKriteria: (id: string, data: Partial<Kriteria>) => void
  deleteKriteria: (id: string) => void

  setSubKriteria: (data: SubKriteria[]) => void
  addSubKriteria: (data: Omit<SubKriteria, "id">) => void
  updateSubKriteria: (id: string, data: Partial<SubKriteria>) => void
  deleteSubKriteria: (id: string) => void

  setPenilaian: (data: Penilaian[]) => void
  updatePenilaian: (alternatifId: string, kriteriaId: string, nilai: number) => void

  setMetodeConfig: (data: MetodeConfig[]) => void
  toggleMetode: (nama: "SAW" | "AHP" | "TOPSIS") => void
  updateMetodeBobot: (nama: "SAW" | "AHP" | "TOPSIS", bobot: number) => void

  setPairwiseComparisons: (data: PairwiseComparison[]) => void
  updatePairwiseComparison: (kriteria1Id: string, kriteria2Id: string, nilai: number) => void
}

const SPKContext = createContext<SPKContextType | null>(null)

export function SPKProvider({ children }: { children: ReactNode }) {
  const [alternatif, setAlternatif] = useState<Alternatif[]>([])
  const [kriteria, setKriteria] = useState<Kriteria[]>([])
  const [subKriteria, setSubKriteria] = useState<SubKriteria[]>([])
  const [penilaian, setPenilaian] = useState<Penilaian[]>([])
  const [metodeConfig, setMetodeConfig] = useState<MetodeConfig[]>([])
  const [pairwiseComparisons, setPairwiseComparisons] = useState<PairwiseComparison[]>([])
  const { showToast } = useToast()

  useEffect(() => {
    const load = async () => {
      try {
        const response = await fetch("/api/spk", { cache: "no-store" })
        if (!response.ok) throw new Error("Gagal memuat data")
        const data = await response.json()

        setAlternatif(
          data.alternatif.map((item: Alternatif) => ({
            ...item,
            createdAt: item.createdAt ? new Date(item.createdAt) : new Date(),
          })),
        )
        setKriteria(
          data.kriteria.map((item: Kriteria) => ({
            ...item,
            createdAt: item.createdAt ? new Date(item.createdAt) : new Date(),
          })),
        )
        setSubKriteria(data.subKriteria)
        setPenilaian(data.penilaian)
        setMetodeConfig(data.metodeConfig)
        setPairwiseComparisons(data.pairwiseComparisons)
      } catch (error) {
        console.error(error)
      }
    }

    load()
  }, [])

  const request = async <T,>(path: string, init?: RequestInit): Promise<T | null> => {
    try {
      const response = await fetch(path, {
        ...init,
        headers: {
          "Content-Type": "application/json",
          ...(init?.headers || {}),
        },
      })
      if (!response.ok) {
        console.warn(`[SPK] request failed for ${path}: ${response.status}`)
        showToast("Gagal terhubung ke server. Periksa koneksi database dan coba lagi.", "error")
        return null
      }
      return (await response.json()) as T
    } catch (error) {
      console.warn(`[SPK] request error for ${path}`, error)
      showToast("Gagal terhubung ke server. Periksa koneksi database dan coba lagi.", "error")
      return null
    }
  }

  const addAlternatif = (data: Omit<Alternatif, "id" | "createdAt">) => {
    void request<Alternatif>("/api/alternatif", {
      method: "POST",
      body: JSON.stringify(data),
    })
      .then((created) => {
        if (!created) return
        setAlternatif((prev) => [...prev, { ...created, createdAt: new Date(created.createdAt) }])
      })
      .catch((error) => console.error(error))
  }

  const updateAlternatif = (id: string, data: Partial<Alternatif>) => {
    void request<Alternatif>(`/api/alternatif/${id}`, {
      method: "PUT",
      body: JSON.stringify(data),
    })
      .then((updated) => {
        if (!updated) return
        setAlternatif((prev) =>
          prev.map((a) => (a.id === id ? { ...updated, createdAt: new Date(updated.createdAt) } : a)),
        )
      })
      .catch((error) => console.error(error))
  }

  const deleteAlternatif = (id: string) => {
    void request(`/api/alternatif/${id}`, { method: "DELETE" })
      .then((result) => {
        if (result === null) return
        setAlternatif((prev) => prev.filter((a) => a.id !== id))
        setPenilaian((prev) => prev.filter((p) => p.alternatifId !== id))
      })
      .catch((error) => console.error(error))
  }

  const addKriteria = (data: Omit<Kriteria, "id" | "createdAt">) => {
    void request<Kriteria>("/api/kriteria", {
      method: "POST",
      body: JSON.stringify(data),
    })
      .then((created) => {
        if (!created) return
        setKriteria((prev) => [...prev, { ...created, createdAt: new Date(created.createdAt) }])
      })
      .catch((error) => console.error(error))
  }

  const updateKriteria = (id: string, data: Partial<Kriteria>) => {
    void request<Kriteria>(`/api/kriteria/${id}`, {
      method: "PUT",
      body: JSON.stringify(data),
    })
      .then((updated) => {
        if (!updated) return
        setKriteria((prev) =>
          prev.map((k) => (k.id === id ? { ...updated, createdAt: new Date(updated.createdAt) } : k)),
        )
      })
      .catch((error) => console.error(error))
  }

  const deleteKriteria = (id: string) => {
    void request(`/api/kriteria/${id}`, { method: "DELETE" })
      .then((result) => {
        if (result === null) return
        setKriteria((prev) => prev.filter((k) => k.id !== id))
        setSubKriteria((prev) => prev.filter((s) => s.kriteriaId !== id))
        setPenilaian((prev) => prev.filter((p) => p.kriteriaId !== id))
      })
      .catch((error) => console.error(error))
  }

  const addSubKriteria = (data: Omit<SubKriteria, "id">) => {
    void request<SubKriteria>("/api/sub-kriteria", {
      method: "POST",
      body: JSON.stringify(data),
    })
      .then((created) => {
        if (!created) return
        setSubKriteria((prev) => [...prev, created])
      })
      .catch((error) => console.error(error))
  }

  const updateSubKriteria = (id: string, data: Partial<SubKriteria>) => {
    void request<SubKriteria>(`/api/sub-kriteria/${id}`, {
      method: "PUT",
      body: JSON.stringify(data),
    })
      .then((updated) => {
        if (!updated) return
        setSubKriteria((prev) => prev.map((s) => (s.id === id ? updated : s)))
      })
      .catch((error) => console.error(error))
  }

  const deleteSubKriteria = (id: string) => {
    void request(`/api/sub-kriteria/${id}`, { method: "DELETE" })
      .then((result) => {
        if (result === null) return
        setSubKriteria((prev) => prev.filter((s) => s.id !== id))
      })
      .catch((error) => console.error(error))
  }

  const updatePenilaian = (alternatifId: string, kriteriaId: string, nilai: number) => {
    void request<Penilaian>("/api/penilaian", {
      method: "POST",
      body: JSON.stringify({ alternatifId, kriteriaId, nilai }),
    })
      .then((updated) => {
        if (!updated) return
        setPenilaian((prev) => {
          const exists = prev.some((p) => p.alternatifId === alternatifId && p.kriteriaId === kriteriaId)
          if (exists) {
            return prev.map((p) =>
              p.alternatifId === alternatifId && p.kriteriaId === kriteriaId ? updated : p,
            )
          }
          return [...prev, updated]
        })
      })
      .catch((error) => console.error(error))
  }

  const toggleMetode = (nama: "SAW" | "AHP" | "TOPSIS") => {
    const current = metodeConfig.find((m) => m.nama === nama)
    if (!current) return
    const nextAktif = !current.aktif

    setMetodeConfig((prev) => prev.map((m) => (m.nama === nama ? { ...m, aktif: nextAktif } : m)))

    void request<MetodeConfig>(`/api/metode-config/${nama}`, {
      method: "PATCH",
      body: JSON.stringify({ aktif: nextAktif }),
    })
      .then((updated) => {
        if (!updated) return
        setMetodeConfig((prev) => prev.map((m) => (m.nama === nama ? updated : m)))
      })
      .catch((error) => console.error(error))
  }

  const updateMetodeBobot = (nama: "SAW" | "AHP" | "TOPSIS", bobot: number) => {
    setMetodeConfig((prev) => prev.map((m) => (m.nama === nama ? { ...m, bobot } : m)))

    void request<MetodeConfig>(`/api/metode-config/${nama}`, {
      method: "PATCH",
      body: JSON.stringify({ bobot }),
    })
      .then((updated) => {
        if (!updated) return
        setMetodeConfig((prev) => prev.map((m) => (m.nama === nama ? updated : m)))
      })
      .catch((error) => console.error(error))
  }

  const updatePairwiseComparison = (kriteria1Id: string, kriteria2Id: string, nilai: number) => {
    void request<PairwiseComparison>("/api/pairwise-comparisons", {
      method: "POST",
      body: JSON.stringify({ kriteria1Id, kriteria2Id, nilai }),
    })
      .then((updated) => {
        if (!updated) return
        setPairwiseComparisons((prev) => {
          const exists = prev.some((p) => p.kriteria1Id === kriteria1Id && p.kriteria2Id === kriteria2Id)
          if (exists) {
            return prev.map((p) =>
              p.kriteria1Id === kriteria1Id && p.kriteria2Id === kriteria2Id ? updated : p,
            )
          }
          return [...prev, updated]
        })
      })
      .catch((error) => console.error(error))
  }

  return (
    <SPKContext.Provider
      value={{
        alternatif,
        kriteria,
        subKriteria,
        penilaian,
        metodeConfig,
        pairwiseComparisons,
        setAlternatif,
        addAlternatif,
        updateAlternatif,
        deleteAlternatif,
        setKriteria,
        addKriteria,
        updateKriteria,
        deleteKriteria,
        setSubKriteria,
        addSubKriteria,
        updateSubKriteria,
        deleteSubKriteria,
        setPenilaian,
        updatePenilaian,
        setMetodeConfig,
        toggleMetode,
        updateMetodeBobot,
        setPairwiseComparisons,
        updatePairwiseComparison,
      }}
    >
      {children}
    </SPKContext.Provider>
  )
}

export function useSPK() {
  const context = useContext(SPKContext)
  if (!context) {
    throw new Error("useSPK must be used within SPKProvider")
  }
  return context
}
